import { ContentCategoriesResponse, ContentCategory } from "@rp/react/tanstack-api/content"

type GetAllContentCategoriesFromResponsePagesProps = {
  pages: Array<ContentCategoriesResponse> | undefined
}

// We have to build and add the "all"
const allContentCategory = {
  id: "all",
  name: "All sources",
  logo: "FilterListOutlined",
  sourceCount: null,
}

function getAllContentCategoriesFromResponsePages({
  pages,
}: GetAllContentCategoriesFromResponsePagesProps): Array<ContentCategory> {
  const contentCategories = pages?.flatMap(page => page.results) || []
  const contentCategoriesWithSources = contentCategories.filter(category => category.sourceCount)

  return [allContentCategory, ...contentCategoriesWithSources]
}

export { getAllContentCategoriesFromResponsePages }
